import { Tool, merged } from '../Tool/Tool';
/**
 * 
 * 
 * @param {string} [state=JSON.parse(Tool.localItem('User'))]
 * @param {Object} action
 * @returns Object
 */
const User = (state = JSON.parse(Tool.localItem('User')), action) => {
    switch (action.type) {
        case 'signinSuccess': //
            Tool.localItem('User', JSON.stringify(action.target));
            return action.target;
        case 'signin': //
            Tool.removeLocalItem('User');
            return null;
        default:
            return state;
    }
}

const DB = (_ID = '', setting = {}) => {
    const cb = {
        setDefault: () => {
            var defaults = merged({
                path: '', //href
                loadAnimation: true, //truefalse 
                loadMsg: '', //
                data: null, //
                scrollX: 0, //X
                scrollY: 0, //Y 
                mdrender: true // false  true markdown 
            }, setting);
            return {
                defaults,
                path: {}
            };
        },
        setState: (state, target) => {
            state.path[target.path] = target;
            return merged(state);
        }
    }
    return (state = {}, action = {}) => {
        if (action._ID && action._ID !== _ID) {
            return state;
        } else if (cb[action.type]) {
            return cb[action.type](state, action.target);
        } else {
            return cb.setDefault();
        }
    }
}
const ShowList = DB('ShowList', { offset: 10, nextBtn: true, length: 10, mdrender: false, data: [] }); //
export default { ShowList, User }