import React, { Component } from 'react';
import { connect } from 'react-redux';
import queryString from 'query-string';
import action from '../../State/Action';
import { Tool, merged, config } from '../../Tool/Tool';
import { DataLoad } from '../DataLoad'
import ScrollNextPage from 'scrollnextpage';

const {target} = config;

/**
 * 
 * 
 * @param {Object} mySetting
 * @returns
 */
const Main = (mySetting) => {
    var setting = {
        id: '', //id
        type: 'GET', //
        url: '', //
        data: null, //
        component: <div></div>, //
        success: (state) => { return state; }, 
        error: (state) => { return state; } 
    };

    /**
     * 
     */
    for (let key in mySetting) {
        setting[key] = mySetting[key];
    }

    /**
     * 
     * 
     * @class Index
     * @extends {Component}
     */
    class Index extends Component {
        constructor(props) {
            super(props);

            /**
             * 
             * 
             * @param {Object} props
             */
            this.initState = (props) => {
                var {state, location} = props;
                var {pathname, search} = location;
                this.path = pathname + search;

                if (typeof this.action == 'undefined' && location.action == 'PUSH') {
                    this.action = false;
                } else {
                    this.action = true;
                }

                if (typeof state.path[this.path] === 'object' && state.path[this.path].path === this.path && this.action) {
                    this.state = state.path[this.path];
                } else {
                    this.state = merged(state.defaults); //path
                    this.state.path = this.path;
                    this.action = false;
                }

            }

            /**
             * DOM
             */
            this.readyDOM = () => {
                var {success, error} = this.props.setting;
                var {scrollX, scrollY} = this.state;
                if (this.get) return false; //
                window.scrollTo(scrollX, scrollY); //
                this.get = new ScrollNextPage(this.refs.dataload, {
                    url: target + this.getUrl(),
                    data: this.getData(),
                    start: this.start,
                    load: this.load,
                    error: this.error
                });
            }

            /**
             * 
             */
            this.start = () => {
                this.state.loadAnimation = true;
                this.state.loadMsg = '...';
                this.props.setState(this.state);
            }
            /**
             * 
             * 
             * @param {Object} res
             */
            this.load = (res) => {
                var {state } = this;
                var {data} = res.result;
                if (!data.length && data.length < before.length) {
                    state.nextBtn = false;
                    state.loadMsg = '';
                } else {
                    state.nextBtn = true;
                    state.loadMsg = '';
                }
                Array.prototype.push.apply(state.data, data);
                state.loadAnimation = false;
                state.offset = state.offset + 10;
                this.props.setState(state);
            }

            /**
             * 
             */
            this.error = () => {
                this.state.loadAnimation = false;
                this.state.loadMsg = '';
                this.props.setState(this.state);
            }

            /**
             * url
             */
            this.unmount = () => {
                this.get.end();
                delete this.get;
                delete this.action;
                this.state.scrollX = window.scrollX; //
                this.state.scrollY = window.scrollY;
                this.props.setState(this.state);
            }

            /**
             * ajax url
             * 
             * @returns Object
             */
            this.getUrl = () => {
                var {url} = this.props.setting;
                if (typeof url === 'function') {
                    return url(this.props, this.state);
                } else if (url && typeof url === 'string') {
                    return url;
                } else {
                    return this.props.location.pathname;
                }
            }

            /**
             * 
             * 
             * @returns
             */
            this.getData = () => {
                var {data} = this.props.setting;
                if (typeof data === 'function') {
                    return data(this.props, this.state);
                } else if (data && typeof data === 'string') {
                    return data;
                } else {
                    return queryString.parse(this.props.location.search).type;
                }
            }

            this.initState(this.props);
        }
        render() {
            var {loadAnimation, loadMsg} = this.state;
            return (
                <div>
                    <this.props.setting.component {...this.props} state={this.state} />
                    <div ref="dataload"><DataLoad loadAnimation={loadAnimation} loadMsg={loadMsg} /></div>
                </div>
            );
        }
        /**
         * 
         *  DOM 
         *  this.getDOMNode()  DOM 
         */
        componentDidMount() {
            this.readyDOM();
        }
        /**
         *  props 
         */
        componentWillReceiveProps(np) {
            var {location} = np;
            var {pathname, search} = location;
            var path = pathname + search;
            if (this.path !== path) {
                this.unmount(); //
            }

            this.initState(np);

        }
        /**
         *  DOM 
         *  DOM 
         */
        componentDidUpdate() {
            this.readyDOM();
        }
        /**
         *  DOM 
         * 
         *  componentDidMount  DOM 
         */
        componentWillUnmount() {
            this.unmount(); //
        }

    }
    Index.defaultProps = { setting }
    return connect((state) => { return { state: state[setting.id], User: state.User } }, action(action.id))(Index); //redux
}


export default Main;